import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TratamientoDatosPersonalesComponent } from './tratamiento-datos-personales/tratamiento-datos-personales.component';
import { PoliticaDePrivacidadComponent } from './politica-de-privacidad/politica-de-privacidad.component';


const routes: Routes = [
  
  {path: '', redirectTo: '/tratamiento-de-datos-personales', pathMatch: 'full'},
  { path: 'tratamiento-de-datos-personales', component: TratamientoDatosPersonalesComponent },
  { path: 'app-politica-de-privacidad', component: PoliticaDePrivacidadComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
