import { Component, OnInit, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-politica-de-privacidad',
  templateUrl: './politica-de-privacidad.component.html',
  styleUrls: ['./politica-de-privacidad.component.css']
})
export class PoliticaDePrivacidadComponent implements OnInit, OnDestroy {
  private favicon: HTMLLinkElement;

  constructor() { }

  ngOnInit(): void {
    this.loadFavicon('assets/favicon/ColmedicaFavicons02.png');
  }

  loadFavicon(faviconUrl: string): void {
    this.favicon = document.createElement('link');
    this.favicon.rel = 'shortcut icon';
    this.favicon.href = faviconUrl;
    document.head.appendChild(this.favicon);
  }

  ngOnDestroy(): void {
    if (this.favicon) {
      document.head.removeChild(this.favicon);
    }
  }
}
