import { Component, OnInit, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-tratamiento-datos-personales',
  templateUrl: './tratamiento-datos-personales.component.html',
  styleUrls: ['./tratamiento-datos-personales.component.css']
})
export class TratamientoDatosPersonalesComponent implements OnInit, OnDestroy {
  private favicon: HTMLLinkElement;

  constructor() { }

  ngOnInit(): void {
    this.loadFavicon('assets/favicon/ColmedicaFavicons01.png');
  }

  loadFavicon(faviconUrl: string): void {
    this.favicon = document.createElement('link');
    this.favicon.rel = 'shortcut icon';
    this.favicon.href = faviconUrl;
    document.head.appendChild(this.favicon);
  }

  ngOnDestroy(): void {
    if (this.favicon) {
      document.head.removeChild(this.favicon);
    }
  }
}
